@import url("https://fonts.googleapis.com/css?family=Lato:300,400,700,900");
@media (min-width: 768px) {
  .nosotros {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.nosotros .nosotros_imagen img {
  border-radius: 1rem;
  -webkit-box-shadow: 2px 2px 9px 0px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 2px 2px 9px 0px rgba(0, 0, 0, 0.75);
  box-shadow: 2px 2px 9px 0px rgba(0, 0, 0, 0.75);
}

.nosotros_texto {
  margin-top: 3rem;
}
.nosotros_texto blockquote {
  font-weight: bold;
  font-size: 2.2rem;
  margin: 0;
  color: #06568d;
  padding-left: 1rem 0 3rem 0;
}